import React from 'react';

let olMap;
let olView;
let olOSM;
let olTileLayer;

class Map extends React.PureComponent {
  mapel;
  olmap;

  render() {
    console.log('Map.render')
    if(this.olmap) {
      this.updateMap();
    }
    return (
      <div className="map" ref={(d) => this.mapel = d}>
      <style jsx>{`
.map {
  width: 100%;
  height: 100%;
}
      `}</style>
      </div>
    );
  }

  componentDidMount() {
    console.log('map.componentDidMount');
    olMap = require('ol/map').default;
    olView = require('ol/view').default;
    olOSM = require('ol/source/osm').default;
    olTileLayer = require('ol/layer/tile').default;

    const view = new olView({
      center: [1877730, 6361175],
      zoom: 8,
      maxZoom: 18
    });

    this.olmap = new olMap({
      target: this.mapel,
      layers: [
        new olTileLayer({
          source: new olOSM()
        })
      ],
      view: view
    });
    const map = this.olmap;

    this.updateMap();
  }

  updateMap = function() {
    const map = this.olmap;

    console.log('updating map')
  }

};

export default Map;



