import Head from 'next/head'
import { Modal, Header, Button, List, Segment, Responsive } from 'semantic-ui-react'
import Map from '../component/Map'

export default () => (
  <div className="root">
    <Head>
      <link rel='stylesheet' href='//cdnjs.cloudflare.com/ajax/libs/semantic-ui/2.2.12/semantic.min.css' />
      <link rel='stylesheet' href='/static/ol.css' />
      <meta name="viewport" content="initial-scale=1.0, width=device-width" />
    </Head>
    <Segment
        className="hide-small"
        style={{position: 'fixed', left: '1vmin', bottom: '1vmin', maxWidth: '50vw', zIndex: 1000}}
    >
      <Header>This is an example portal</Header>
      <p>Portals have tons of great callback functions to hook into.</p>
      <p>To close, simply click the close button or click away</p>
    </Segment>

  <Modal
      className="hide-medium hide-large"
      closeOnEscape
      trigger={
        <Button
            color='blue'
            className="hide-medium hide-large"
            style={{position: 'fixed', left: '1vmin', bottom: '1vmin', zIndex: 1000}}
        >i</Button>
      }
  >
    <Header>This is an example portal</Header>
    <p>Portals have tons of great callback functions to hook into.</p>
    <p>To close, simply click the close button or click away</p>
    <Modal.Actions>
      <Button positive>Rozumím</Button>
    </Modal.Actions>
  </Modal>
  <Map>
  </Map>

   <style jsx>{`
.root {
  width: 100%;
  height: 100%;
}
  `}</style>
   <style jsx global>{`
@media (max-width:600px) {
  .hide-small {
    display:none!important;
  }
}
@media (min-width:601px) and (max-width:992px) {
  .hide-medium {
    display:none!important
  }
}
@media (min-width:993px){
  .hide-large {
    display:none!important
  }
}
html, body, #__next {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
  `}</style>
  </div>
)



